import React, { useState, useCallback, useEffect } from 'react';
import { Box, Container, makeStyles } from '@material-ui/core';
import Page from '../../../components/Page';
import useMember from '../../../hooks/useMember';
import useBranch from '../../../hooks/useBranch';
import useIsMountedRef from '../../../hooks/useIsMountedRef';
import Header from './Header';
import { useParams } from 'react-router-dom';
import AddChopsForm from './AddChopsForm';
import { useTranslation } from 'react-i18next';
import consumeChopRuleService from '../../../services/consumeChopRuleService';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

function AddChops() {
  const { id } = useParams();
  const classes = useStyles();
  const { members } = useMember();
  const { branches } = useBranch();
  const { t } = useTranslation();

  if (!members.length) {
    return null;
  }

  if (!branches.length) {
    return null;
  }

  return (
    <Page className={classes.root} title={t('Add Chops')}>
      <Container maxWidth="lg">
        <Header />
        <Box mt={3}>
          <AddChopsForm members={members} branches={branches} />
        </Box>
      </Container>
    </Page>
  );
}

export default AddChops;
