import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import {
  Box,
  Button,
  Card,
  CardContent,
  Grid,
  Switch,
  TextField,
  Typography,
  makeStyles
} from '@material-ui/core';
import { Alert } from '@material-ui/lab';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import chopService from '../../../services/chopService';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles(() => ({
  root: {}
}));

function AddChopsForm({ className, members, branches, ...rest }) {
  const classes = useStyles();
  const { enqueueSnackbar } = useSnackbar();
  const { t } = useTranslation();

  return (
    <Formik
      initialValues={{
        member_id: members[0].phone,
        branch_id: branches[0].code,
        chops: 0
      }}
      validationSchema={Yup.object().shape({
        member_id: Yup.string()
          .max(255)
          .required(t('is required', { name: t('Member') })),
        branch_id: Yup.string()
          .max(255)
          .required(),
        chops: Yup.number()
          .min(1)
          .required(t('is required', { name: t('Chops') }))
      })}
      onSubmit={async (
        values,
        { resetForm, setErrors, setStatus, setSubmitting }
      ) => {
        try {
          await chopService.add({
            ...values
          });
          setStatus({ success: true });
          setSubmitting(false);
          enqueueSnackbar(t('Chops added'), {
            variant: 'success'
          });
        } catch (error) {
          setStatus({ success: false });
          setErrors({ submit: error.response.data.message });
          setSubmitting(false);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        touched,
        values
      }) => (
        <form
          className={clsx(classes.root, className)}
          onSubmit={handleSubmit}
          {...rest}
        >
          <Card>
            <CardContent>
              {errors.submit && (
                <Box mb={3}>
                  <Alert severity="error">{errors.submit}</Alert>
                </Box>
              )}
              <Grid container spacing={3}>
                <Grid item md={6} xs={12}>
                  <TextField
                    fullWidth
                    label={t('Member')}
                    name="member_id"
                    onChange={handleChange}
                    select
                    required
                    SelectProps={{ native: true }}
                    value={values.member_id}
                    variant="outlined"
                  >
                    {members.map((member) => (
                      <option key={member.phone} value={member.phone}>
                        {member.first_name} {member.last_name}
                      </option>
                    ))}
                  </TextField>
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    fullWidth
                    label={t('Branch')}
                    name="branch_id"
                    onChange={handleChange}
                    select
                    required
                    SelectProps={{ native: true }}
                    value={values.branch_id}
                    variant="outlined"
                  >
                    {branches.map((branch) => (
                      <option key={branch.code} value={branch.code}>
                        {branch.name}
                      </option>
                    ))}
                  </TextField>
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.chops && errors.chops)}
                    fullWidth
                    helperText={touched.chops && errors.chops}
                    label={t('Chops')}
                    name="chops"
                    type="number"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.chops}
                    variant="outlined"
                  />
                </Grid>
              </Grid>
              <Box mt={2}>
                <Button
                  variant="contained"
                  color="secondary"
                  type="submit"
                  disabled={isSubmitting}
                >
                  {t('Add Chops')}
                </Button>
              </Box>
            </CardContent>
          </Card>
        </form>
      )}
    </Formik>
  );
}

AddChopsForm.propTypes = {
  className: PropTypes.string,
  members: PropTypes.array.isRequired,
  branches: PropTypes.array.isRequired
};

export default AddChopsForm;
